import { mapGetters, mapMutations } from 'vuex'

export default {
  name: "Item",
  props: {
    item: Object
  },
  methods: {
    ...mapMutations({
        addToCart: 'localStorage/add',
        removeFromCart: 'localStorage/remove',
    }),
  },
  computed: {
    ...mapGetters ({
        getItemPrice: 'localStorage/itemPrice',
    }),
  },
};
